<template>
  <v-content
    id="lessonmanage"
    class="bg-grey-full-page">
    <div
      v-if="pageView"
      class="container"
      style="min-height: calc(100vh - 70px - 145px);">
      <p
        class="mb-5 h3 font-weight-bold"
        style="word-wrap: break-word; white-space: pre-wrap;text-align: center;font-weight: 500!important;"></p>
      <v-btn
        class="p-4 h5 bg-button-color lesson-open"
        @click="$router.push('/lesson/ready/add')">
        <v-icon class="mr-2">mdi-plus</v-icon>
      </v-btn>
      <div class="lesson-manage-area">
        <div class="lesson-manage-title">
          <div
            :class="{'active': type==='selling'}"
            class="cursor-no-hover"
            @click="type='selling'">
            <span></span>
            <v-icon>mdi-menu-up</v-icon>
          </div>
          <div
            :class="{'active': type==='draft'}"
            class="cursor-no-hover"
            @click="type='draft'">
            <span></span>
            <v-icon>mdi-menu-up</v-icon>
          </div>
          <div
            :class="{'active': type==='notSelling'}"
            class="cursor-no-hover"
            @click="type='notSelling'">
            <span></span>
            <v-icon>mdi-menu-up</v-icon>
          </div>
          <div/>
          <div class="p-0">
            <v-btn
              class="m-auto px-3 py-2 h5 bg-button-color"
              @click="$router.push('/lesson/ready/add')">
              <v-icon class="mr-1">mdi-plus</v-icon>
            </v-btn>
          </div>
        </div>
        <div class="lesson-manage-item">
          <div
            v-for="(lesson, l) in lessonList"
            :key="'l'+l"
            :class="{'d-none': lesson.lesson_status!==type}"
            class="lesson-manage-unit">
            <div
              :style="'background-image: url(\'' + root + lesson.background_picture_path + '?t=' + new Date().getTime() + '\');'"
              class="bg"/>
            <div class="main">
              <div class="title">
                <span v-if="lesson.lesson_status!=='selling'">{{ lesson.lesson_title }}</span>
                <span
                  v-if="lesson.lesson_status==='selling'"
                  class="cursor-no-hover"
                  @click="$router.push('/lesson/main/view/'+lesson.lesson_id)">{{ lesson.lesson_title }}</span>
                <span>NT${{ lesson.price_per_hour }}</span>
              </div>
              <div class="intro">
                {{ lesson.brief_lesson_intro }}...
              </div>
              <div class="program">
                <span
                  v-for="(sales, s) in lesson.sales_package"
                  :key="'s'+s"
                  class="bg-main-color">{{ sales }}</span>
              </div>
            </div>
            <div class="action">
              <v-chip
                class="cursor-no-hover h5 cursor"
                @click="$router.push('/lesson/main/preview/'+lesson.lesson_id)"></v-chip>
              <v-chip
                class="cursor-no-hover h5 cursor"
                @click="$router.push('/lesson/ready/edit/'+lesson.lesson_id)"></v-chip>
              <v-chip
                v-if="lesson.lesson_status!=='selling'"
                class="cursor-no-hover h5 cursor"
                @click="doLessonStatus(lesson.lesson_id,'selling')"></v-chip>
              <v-chip
                v-if="lesson.lesson_status==='selling'"
                class="cursor-no-hover h5 cursor"
                @click="doLessonStatus(lesson.lesson_id,'notSelling')"></v-chip>
              <v-chip
                class="cursor-no-hover h5 cursor del"
                @click="doLessonStatus(lesson.lesson_id,'donotShow')"></v-chip>
            </div>
            <div class="delete">
              <v-icon
                class="cursor"
                @click="doLessonStatus(lesson.lesson_id,'donotShow')">mdi-trash-can</v-icon>
            </div>
          </div>
        </div>
      </div>
    </div>
    <v-dialog
      v-model="loadingView"
      persistent
      >
      <p style="letter-spacing: 5px;text-align: center !important;color: #ffffff;background-color: #000000dd;padding: 15px;margin: auto;">
      <img
      :src="loading"
      style="width: 2rem;margin-bottom: 10px;">
      <br>
      {{ loadingViewText }}
      </p>
    </v-dialog>
    <v-snackbar
      v-model="snackbar"
      :top="true"
      :color="color">
      {{ errorMessages }}
      <v-btn
        dark
        flat
        @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </v-content>
</template>

<script>
import qs from 'qs'
import prove from '@/assets/img/prove.png'
import cardBg1 from '@/assets/img/lesson_card_bg1.jpg'
import cardBg2 from '@/assets/img/lesson_card_bg2.jpg'
import cardBg3 from '@/assets/img/lesson_card_bg3.jpg'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
export default {
  name: 'LessonManage',
  metaInfo () {
    return {
      title: this.appName + ''
    }
  },
  data: function () {
    return {
      loadingView: false,
      loadingViewText: '',
      pageView: false,
      type: 'selling',
      prove,
      cardBg1,
      cardBg2,
      cardBg3,
      loading,
      snackbar: false,
      color: 'red',
      errorMessages: '',
      lessonList: [
        // {
        //   lesson_status: 'draft',
        //   price_per_hour: '500',
        //   brief_lesson_intro: '',
        //   background_picture_code: 99,
        //   background_picture_path: cardBg1,
        //   lesson_title: '',
        //   sales_package: [' $100', '', '100 80%', '100 80%', '100 80%']
        // },
        // {
        //   lesson_status: 'selling',
        //   price_per_hour: '300',
        //   brief_lesson_intro: '',
        //   background_picture_code: 99,
        //   background_picture_path: cardBg1,
        //   lesson_title: '',
        //   sales_package: [' $100', '', '100 80%', '100 80%', '100 80%']
        // }
      ]
    }
  },
  computed: {
    ...mapState(['root', 'appName', 'dologinView']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  watch: {
    dologinView (val) {
      if (!val) {
        this.$router.push('/')
      }
    }
  },
  mounted () {
    if (localStorage.getItem('token') && this.userInfo.type === 'teacher') {
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              this.getLessonListInfo()
            } else {
              this.$router.push('/')
            }
          } else if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
        .catch(err => {
          console.log(err)
          if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else if (!localStorage.getItem('token')) {
      this.$store.state.dologinView = true
    } else {
      this.$router.push('/')
    }
  },
  methods: {
    test () {
      alert('')
    },
    doLessonStatus (id, status) {
      if (localStorage.getItem('token')) {
        this.loadingViewText = ((status === 'selling') ? '' : (status === 'notSelling') ? '' : '') + ''
        this.loadingView = true
        let body = {
          userID: (this.userInfo ? this.userInfo.user_id : -1),
          lessonID: id,
          selling_status: status
        }
        this.$http.post('/api/lesson/setLessonStatus/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              this.getLessonListInfo()
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '' + ((status === 'selling') ? '' : (status === 'notSelling') ? '' : '') + ''
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = ''
            }
          })
          .catch(err => {
            this.loadingView = false
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = ''
            console.log(err)
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    getLessonListInfo () {
      this.$http.get(`/api/lesson/getLessonCardsForTheTeacherWhoCreatedThem/?action=showOpenLesson&teacherID=${this.userInfo.user_id}&userID=${this.userInfo.user_id}`).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (this.userInfo.type === 'teacher') {
              this.pageView = true
              this.lessonList = res.data
              this.lessonList.forEach((l, index) => {
                if (l.background_picture_code === 0) {
                  this.lessonList[index].background_picture_path = this.cardBg1
                } else if (l.background_picture_code === 1) {
                  this.lessonList[index].background_picture_path = this.cardBg2
                } else if (l.background_picture_code === 2) {
                  this.lessonList[index].background_picture_path = this.cardBg3
                }
              })
            } else {
              this.$router.push('/')
            }
          } else if (res.status === 'failed') {
            // this.snackbar = true
            // this.color = 'red'
            // this.errorMessages = res.errMsg
            this.$router.go(-1)
          } else {
            // this.snackbar = true
            // this.color = 'red'
            // this.errorMessages = ''
            this.$router.go(-1)
          }
        })
        .catch(err => {
          // this.snackbar = true
          // this.color = 'orange'
          // this.errorMessages = ''
          console.log(err)
          this.$router.go(-1)
        })
    }
  }
}
</script>
