            <strong style="color: red;">Email</strong><br>
            <strong style="color: red;"></strong>
          </p>
          <v-textarea
            v-model="regNotifiemail"
            :hint="regNotifiemailMessages"
            :class="{'fail': regNotifiemailMessages.indexOf('')!==-1, 'pass': regNotifiemailMessages.indexOf('')!==-1}"
            label=""
            rows="1"
            type="text"
            persistent-hint/>
        </div>
      </div>
      <hr>
      <p class="h3"><span class="h4 mr-1 text-muted">()</span></p>
      <div class="row">
        <div class="col-xs-12 col-md-6">
          <v-text-field
            v-model="regInvitationCode"
            :hint="regInvitationCodeMessages"
            :class="{'required-fail': regInvitationCode==='', 'fail': regInvitationCodeMessages.indexOf('')!==-1, 'pass': regInvitationCodeMessages.indexOf('')!==-1}"
            label=""
            type="text"
            persistent-hint/>
        </div>
      </div>
      <hr>
      <p class="h3"></p>
      <div class="row">
        <div class="col-xs-12 col-md-12">
          <p
            class="mx-3 mb-4 h4"
