            @click="userAgreementOpen=false">
            <v-icon class="mr-2">mdi-chevron-up</v-icon>
          </v-btn> -->
          <v-checkbox
            v-model="userAgreementCheck"
            class="mx-3 user-agreement-check"
            label=""/>
        </div>
      </div>
      <hr>
      <div class="row">
        <div class="col-xs-12 col-md-12 text-center">
          <v-btn
            :disabled="!userAgreementCheck"
            class="mx-0 my-4 p-4 h5 bg-button-color w-50"
            @click="doSubmit()">
            <v-icon class="mr-2">mdi-account-plus</v-icon>
          </v-btn>
        </div>
        <!-- <pre style="word-wrap: break-word; white-space: pre-wrap;">{{ msg }}</pre> -->
        <!-- <v-flex
          xs12
          lg2>
          <v-menu
            :v-model="false"
            :close-on-content-click="false"
            :nudge-left="40"
            lazy
            transition="scale-transition"
            max-width="290px"
            offset-y
            full-width
          >
            <v-text-field
              slot="activator"
              :class="{'danger-input' : theme==='dark' , 'general-input' : theme==='light'}"
              v-model="errorcode"
              label=""
              prepend-icon="event"
            />
            <v-date-picker
              v-model="errorcode"
              :min="new Date()"
              :max="new Date()"
              :color="theme==='dark'?'danger':'general'"
              type="month"
              no-title
              scrollable
              actions/>
          </v-menu>
        </v-flex> -->
      </div>
    </div>
    <v-dialog
      v-model="loadingView"
      persistent
      >
      <p style="letter-spacing: 5px;text-align: center !important;color: #ffffff;background-color: #000000dd;padding: 15px;margin: auto;">
      <img
      :src="loading"
      style="width: 2rem;margin-bottom: 10px;">
      <br>
      {{ loadingViewText }}
      </p>
    </v-dialog>
    <v-snackbar
      v-model="snackbar"
      :top="true"
      :color="color">
      {{ errorMessages }}
      <v-btn
        dark
        flat
        @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
