<script>
import $ from 'jquery'
import qs from 'qs'
import sha256 from 'sha256'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
import UserAgreement from '../Plugins/UserAgreement.vue'
export default {
  name: 'StudentRegister',
  components: {
    UserAgreement
  },
  metaInfo () {
    return {
      title: this.appName + ''
    }
  },
  data: function () {
    return {
      loadingView: false,
      loadingViewText: '',
      loading,
      snackbar: false,
      color: 'red',
      errorMessages: '',
      regEmail: '',
      regEmailMessages: 'Email',
      regPwd: '',
      regPwdMessages: '8',
      regPwdConfirm: '',
      regPwdConfirmMessages: '',
      regMobile: '',
      regMobileMessages: '09XX-XXXXXX',
      regName: '',
      regNameMessages: '',
      regNickname: '',
      regNicknameMessages: '',
      regRole: '',
      regRoleMessages: '',
      regGender: '',
      regGenderMessages: '',
      regBirth: '',
      // regBirth: this.$moment(new Date('2000-01-01')).format('YYYY-MM-DD'),
      regBirthMessages: '1900-01-01 ()',
      // regBirthMenu: false,
      regHeadshot: [
        {
          file: '',
          name: '',
          url: ''
        }
      ],
      regNotifiemail: '',
      regNotifiemailMessages: ';Emailyou@cool.com;we@awesome.com',
      regInvitationCode: '',
      regInvitationCodeMessages: '()',
      userAgreementOpen: false,
      userAgreementCheck: false,
      // submitVerify: false,
      submit: false
    }
  },
  computed: {
    ...mapState(['root', 'appName', 'isProduction']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  watch: {
    regEmail (val) {
      let isEmail = /^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-|_)[A-Za-z0-9]+)*\.[A-Za-z]+$/
      if (this.regEmail === '') {
        this.regEmailMessages = ' Email'
      } else if (!isEmail.test(this.regEmail)) {
        this.regEmailMessages = ' Email'
      } else {
        this.regEmailMessages = ''
      }
    },
    regPwd (val) {
      if (this.regPwd.length < 8) {
        this.regPwdMessages = ' 8'
      } else {
        this.regPwdMessages = ''
      }
    },
    regPwdConfirm (val) {
      if (this.regPwdConfirm === '') {
        this.regPwdConfirmMessages = ' '
      } else if (this.regPwdConfirm !== this.regPwd) {
        this.regPwdConfirmMessages = ' '
      } else if (this.regPwdConfirm.length < 8) {
        this.regPwdMessages = ' 8'
      } else {
        this.regPwdConfirmMessages = ''
      }
    },
    regMobile (val) {
      let isMobile = /^09[0-9]{2}-[0-9]{6}$/
      if (this.regMobile === '') {
        this.regMobileMessages = ' 09XX-XXXXXX'
      } else if (this.regMobile.length === 4) {
        this.regMobile = this.regMobile + '-'
      } else if (!isMobile.test(this.regMobile)) {
        this.regMobileMessages = ' 09XX-XXXXXX'
      } else {
        this.regMobileMessages = ''
      }
    },
    regName (val) {
      if (this.regName === '') {
        this.regNameMessages = ' '
      } else {
        this.regNameMessages = ''
      }
    },
    regNickname (val) {
      if (this.regNickname !== '') {
        this.regNicknameMessages = ''
      } else {
        this.regNicknameMessages = ''
      }
    },
    regRole (val) {
      if (this.regRole === '') {
        this.regRoleMessages = ' '
      } else {
        this.regRoleMessages = ''
      }
    },
    regGender (val) {
      if (this.regGender === '') {
        this.regGenderMessages = ' '
      } else {
        this.regGenderMessages = ''
      }
    },
    regBirth (val) {
      // let isBirth = /^[0-9]{4}-[0-9]{2}-[0-9]{2}$/
      // if (this.regBirth !== '' && !isBirth.test(this.regBirth)) {
      //   this.regBirthMessages = ' 1900-01-01 ()'
      // } else if (this.regBirth !== '') {
      //   this.regBirthMessages = ''
      // } else {
      //   this.regBirthMessages = ' 1900-01-01 ()'
      // }
      let isBirth = /^[0-9]{4}-[0-9]{2}-[0-9]{2}$/
      if (this.regBirth === '') {
        this.regBirthMessages = ' 1900-01-01 ()'
      } else if (this.regBirth.length === 4) {
        this.regBirth = this.regBirth + '-'
      } else if (this.regBirth.length === 7) {
        this.regBirth = this.regBirth + '-'
      } else if (!isBirth.test(this.regBirth)) {
        this.regBirthMessages = ' 1900-01-01 ()'
      } else {
        this.regBirthMessages = ''
      }
    },
    regNotifiemail (val) {
      if (this.regNotifiemail !== '' && this.regNotifiemailArray()) {
        this.regNotifiemailMessages = ' Emailyou@cool.com;we@awesome.com'
      } else if (this.regNotifiemail !== '') {
        this.regNotifiemailMessages = ''
      } else {
        this.regNotifiemailMessages = ';Emailyou@cool.com;we@awesome.com'
      }
    },
    regInvitationCode (val) {
      if (this.regInvitationCode !== '') {
        this.regInvitationCodeMessages = ' ()'
      } else {
        this.regInvitationCodeMessages = '()'
      }
    }
  },
  mounted () {
    let publicPage = true
    if (!localStorage.getItem('token') && publicPage) {
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              if (this.$route.params.code) {
                this.regInvitationCode = this.$route.params.code
              }
            } else {
              // this.$router.push('/')
            }
          } else if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
        .catch(err => {
          console.log(err)
          if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else if (localStorage.getItem('token')) {
      this.$router.push('/')
    } else {
      // this.$router.push('/')
    }
  },
  methods: {
    regNotifiemailArray () {
      let error = false
      let isNotifiemail = /^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-|_)[A-Za-z0-9]+)*\.[A-Za-z]+$/
      let list = this.regNotifiemail.split(';')
      list.forEach((l) => {
        if (!isNotifiemail.test(l)) {
          error = true
        }
      })
      return error
    },
    doGtagsEvent (name, description, remarks) {
      if (this.isProduction) {
        this.$gtag.event(name, {
          description: description,
          remarks: remarks,
          page_path: this.$route.fullPath,
          hostname: this.root,
          user_id: (this.userInfo ? this.userInfo.user_id : -1)
        })
      }
    },
    setHeadshot (e) {
      let files = e.target.files || e.dataTransfer.files
      if (e.srcElement.files.item(0).size > (4 * 1024 * 1024)) {
        $('#headshot-file').val('')
        alert('4MB')
      } else {
        // alert('Uploading: ' + e.srcElement.files.item(0).name + ' @ ' + e.srcElement.files.item(0).size + 'bytes')
        // console.log(window.URL.createObjectURL(e.srcElement.files.item(0)))
        // console.log(e.srcElement.files.item(0).name)
        // let target = e.target
        // console.log(this.uploadIMG[i].url)
        // this.uploadIMG[i].url = this.getFileUrl(e.srcElement)
        this.regHeadshot[0].url = window.URL.createObjectURL(e.srcElement.files.item(0))
        this.regHeadshot[0].file = files[0]
        this.regHeadshot[0].name = e.srcElement.files.item(0).name
        // $('#img_' + target.id).attr('src', this.getFileUrl(e.srcElement))
      }
    },
    cancelHeadshotIMG () {
      this.regHeadshot[0].file = this.regHeadshot[0].name = this.regHeadshot[0].url = ''
      $('#headshot-file').val('')
      // this.createImage(files[0])
    },
    dosubmitVerifyBefore () {
      this.submitVerify = true
    },
    doSubmitVerify () {
      if (this.regEmailMessages.indexOf('') === -1 &&
      this.regEmail !== '' &&
      this.regPwdMessages.indexOf('') === -1 &&
      this.regPwd !== '' &&
      this.regPwdConfirmMessages.indexOf('') === -1 &&
      this.regPwdConfirm !== '' &&
      this.regMobileMessages.indexOf('') === -1 &&
      this.regMobile !== '' &&
      this.regNameMessages.indexOf('') === -1 &&
      this.regName !== '' &&
      this.regNicknameMessages.indexOf('') === -1 &&
      this.regRoleMessages.indexOf('') === -1 &&
      this.regRole !== '' &&
      this.regGenderMessages.indexOf('') === -1 &&
      this.regGender !== '' &&
      this.regBirthMessages.indexOf('') === -1 &&
      this.regBirth !== '' &&
      this.regNotifiemailMessages.indexOf('') === -1 &&
      this.regInvitationCodeMessages.indexOf('') === -1) {
        this.submit = true
      } else {
        this.submit = false
        scroll(0, 0)
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = ''
      }
    },
    dosubmitVerifyAfter () {
      if (this.submit) {
        this.loadingViewText = ''
        this.loadingView = true
        let formData = new FormData()
        formData.append('regEmail', this.regEmail)
        formData.append('regPwd', sha256(this.regPwd).toUpperCase())
        formData.append('regMobile', this.regMobile)
        formData.append('regName', this.regName)
        formData.append('regNickname', this.regNickname || this.regName)
        formData.append('regRole', this.regRole)
        formData.append('regGender', this.regGender)
        formData.append('regBirth', this.regBirth)
        formData.append('upload_snapshot', this.regHeadshot[0].file)
        formData.append('regNotifiemail', this.regNotifiemail)
        formData.append('invitation_code', this.regInvitationCode)
        this.$http.post('api/account/signupStudent/', formData).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              this.doGtagsEvent('StudentRegister-', '[]', this.regEmail)
              // scroll(0, 0)
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = ''
              alert('!')
              setTimeout(() => {
                this.$router.push('/')
              }, 500)
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = ''
            }
          })
          .catch(err => {
            this.loadingView = false
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = ''
            console.log(err)
          })
      }
    },
    async doSubmit () {
      await this.dosubmitVerifyBefore()
      await this.doSubmitVerify()
      await this.dosubmitVerifyAfter()
    }
  }
}
</script>
