<script>
import $ from 'jquery'
import qs from 'qs'
import sha256 from 'sha256'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
import TimeSelector from '../Plugins/TimeSelector.vue'
import UserAgreement from '../Plugins/UserAgreement.vue'
export default {
  name: 'TeacherRegister',
  components: {
    TimeSelector,
    UserAgreement
  },
  metaInfo () {
    return {
      title: this.appName + ''
    }
  },
  data: function () {
    return {
      loadingView: false,
      loadingViewText: '',
      loading,
      snackbar: false,
      color: 'red',
      errorMessages: '',
      timeID: '',
      random: '',
      areaIntro: false,
      areaTag: false,
      areaProve: false,
      areaHeadshot: false,
      areaTime: false,
      // resConfirm: false,
      regEmail: '',
      regEmailMessages: 'Email',
      regPwd: '',
      regPwdMessages: '8',
      regPwdConfirm: '',
      regPwdConfirmMessages: '',
      regMobile: '',
      regMobileMessages: '09XX-XXXXXX',
      regName: '',
      regNameMessages: '()',
      regNickname: '',
      regNicknameMessages: '',
      regYear: '',
      regYearMessages: '',
      regGender: '',
      regGenderMessages: '',
      regBirth: '',
      // regBirth: this.$moment(new Date('2000-01-01')).format('YYYY-MM-DD'),
      regBirthMessages: '1900-01-01 ()',
      // regBirthMenu: false,
      regIntro: '',
      regIntroMessages: '',
      regTag: '',
      regTagMessages: '0/500',
      regEdu1: '',
      regEdu1Messages: '/',
      regEdu2: '',
      regEdu2Messages: '/',
      regEdu3: '',
      regEdu3Messages: '/',
      regJob: '',
      regJobMessages: '/',
      regExp: '',
      regExpMessages: '',
      regHeadshot: [
        {
          file: '',
          name: '',
          url: ''
        }
      ],
      // regHeadshotMessages: '',
      regSelectTime: [],
      // regSelectTimeMessages: '',
      regInvitationCode: '',
      regInvitationCodeMessages: '()',
      uploadIMG: [
        {
          file: '',
          name: '',
          url: ''
        }, {
          file: '',
          name: '',
          url: ''
        }
      ],
      userAgreementOpen: false,
      userAgreementCheck: false,
      submitMessages: '',
      submit: false
    }
  },
  computed: {
    ...mapState(['root', 'appName', 'isProduction']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  watch: {
    resSelectTime (val) {
      this.regSelectTime = val
    },
    regEmail (val) {
      let isEmail = /^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-|_)[A-Za-z0-9]+)*\.[A-Za-z]+$/
      if (this.regEmail === '') {
        this.regEmailMessages = ' Email'
      } else if (!isEmail.test(this.regEmail)) {
        this.regEmailMessages = ' Email'
      } else {
        this.regEmailMessages = ''
      }
    },
    regPwd (val) {
      if (this.regPwd.length < 8) {
        this.regPwdMessages = ' 8'
      } else {
        this.regPwdMessages = ''
      }
    },
    regPwdConfirm (val) {
      if (this.regPwdConfirm === '') {
        this.regPwdConfirmMessages = ' '
      } else if (this.regPwdConfirm !== this.regPwd) {
        this.regPwdConfirmMessages = ' '
      } else if (this.regPwdConfirm.length < 8) {
        this.regPwdMessages = ' 8'
      } else {
        this.regPwdConfirmMessages = ''
      }
    },
    regMobile (val) {
      let isMobile = /^09[0-9]{2}-[0-9]{6}$/
      if (this.regMobile === '') {
        this.regMobileMessages = ' 09XX-XXXXXX'
      } else if (this.regMobile.length === 4) {
        this.regMobile = this.regMobile + '-'
      } else if (!isMobile.test(this.regMobile)) {
        this.regMobileMessages = ' 09XX-XXXXXX'
      } else {
        this.regMobileMessages = ''
      }
    },
    regName (val) {
      if (this.regName === '') {
        this.regNameMessages = ' '
      } else {
        this.regNameMessages = ''
      }
    },
    regNickname (val) {
      if (this.regNickname !== '') {
        this.regNicknameMessages = ''
      } else {
        this.regNicknameMessages = ''
      }
    },
    regYear (val) {
      if (this.regYear === '') {
        this.regYearMessages = ' '
      } else {
        this.regYearMessages = ''
      }
    },
    regGender (val) {
      if (this.regGender === '') {
        this.regGenderMessages = ' '
      } else {
        this.regGenderMessages = ''
      }
    },
    regBirth (val) {
      // let isBirth = /^[0-9]{4}-[0-9]{2}-[0-9]{2}$/
      // if (this.regBirth !== '' && !isBirth.test(this.regBirth)) {
      //   this.regBirthMessages = ' 1900-01-01 ()'
      // } else if (this.regBirth !== '') {
      //   this.regBirthMessages = ''
      // } else {
      //   this.regBirthMessages = ' 1900-01-01 ()'
      // }
      let isBirth = /^[0-9]{4}-[0-9]{2}-[0-9]{2}$/
      if (this.regBirth === '') {
        this.regBirthMessages = ' 1900-01-01 ()'
      } else if (this.regBirth.length === 4) {
        this.regBirth = this.regBirth + '-'
      } else if (this.regBirth.length === 7) {
        this.regBirth = this.regBirth + '-'
      } else if (!isBirth.test(this.regBirth)) {
        this.regBirthMessages = ' 1900-01-01 ()'
      } else {
        this.regBirthMessages = ''
      }
    },
    regIntro (val) {
      if (this.regIntro === '') {
        this.regIntroMessages = ''
      } else {
        this.regIntroMessages = ''
      }
    },
    regTag (val) {
      if (this.regTag === '') {
        this.regTagMessages = val.length + '/500'
      } else if (this.regTag.length > 500) {
        this.regTagMessages = ' ' + val.length + '/500'
      } else {
        this.regTagMessages = ' ' + val.length + '/500'
      }
    },
    regEdu1 (val) {
