      // } else if (this.regEdu1 === '' && this.regEdu2 === '' && this.regEdu3 === '' && this.regJob === '' && this.regExp === '') {
      //   this.regJobVerify = true
      //   return ''
      } else {
        this.regJobMessages = '/'
      }
    },
    regExp (val) {
      if (this.regExp !== '') {
        this.regExpMessages = ''
      // } else if (this.regEdu1 === '' && this.regEdu2 === '' && this.regEdu3 === '' && this.regJob === '' && this.regExp === '') {
      //   this.regExpVerify = true
      //   return ''
      } else {
        this.regExpMessages = ''
      }
    },
    regInvitationCode (val) {
      if (this.regInvitationCode !== '') {
        this.regInvitationCodeMessages = ' ()'
      } else {
        this.regInvitationCodeMessages = '()'
      }
    }
    // regHeadshot (val) {
    //   if (this.submitVerify) {
    //     if ($('#headshot-file').val() === '') {
    //       this.regHeadshotVerify = true
    //       return ''
    //     } else {
    //       this.regHeadshotVerify = false
    //       return ''
    //     }
    //   } else {
    //     this.regHeadshotVerify = false
    //     return ''
    //   }
    // },
    // regSelectTime (val) {
    //   if (this.submitVerify) {
    //     if (this.regSelectTime === []) {
