  },
  mounted () {
    let publicPage = true
    if (!localStorage.getItem('token') && publicPage) {
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              if (this.$route.params.code) {
                this.regInvitationCode = this.$route.params.code
              }
              if (this.$route.params.nickname) {
                this.regNickname = this.$route.params.nickname
              }
              if (this.$route.params.male) {
                this.regGender = this.$route.params.male
              }
              if (this.$route.params.time) {
                this.timeID = this.$route.params.time
              }
              if (this.$route.params.random) {
                this.random = this.$route.params.random
              }
            } else {
              // this.$router.push('/')
            }
          } else if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
        .catch(err => {
          console.log(err)
          if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else if (localStorage.getItem('token')) {
      this.$router.push('/')
    } else {
      // this.$router.push('/')
    }
  },
  methods: {
    doGtagsEvent (name, description, remarks) {
      if (this.isProduction) {
        this.$gtag.event(name, {
          description: description,
          remarks: remarks,
          page_path: this.$route.fullPath,
          hostname: this.root,
          user_id: (this.userInfo ? this.userInfo.user_id : -1)
        })
      }
    },
    setHeadshot (e) {
      let files = e.target.files || e.dataTransfer.files
      if (e.srcElement.files.item(0).size > (4 * 1024 * 1024)) {
        $('#headshot-file').val('')
        alert('4MB')
      } else {
        // alert('Uploading: ' + e.srcElement.files.item(0).name + ' @ ' + e.srcElement.files.item(0).size + 'bytes')
        // console.log(window.URL.createObjectURL(e.srcElement.files.item(0)))
        // console.log(e.srcElement.files.item(0).name)
        // let target = e.target
        // console.log(this.uploadIMG[i].url)
        // this.uploadIMG[i].url = this.getFileUrl(e.srcElement)
        this.regHeadshot[0].url = window.URL.createObjectURL(e.srcElement.files.item(0))
        this.regHeadshot[0].file = files[0]
        this.regHeadshot[0].name = e.srcElement.files.item(0).name
        // $('#img_' + target.id).attr('src', this.getFileUrl(e.srcElement))
      }
    },
    cancelHeadshotIMG () {
      this.regHeadshot[0].file = this.regHeadshot[0].name = this.regHeadshot[0].url = ''
      $('#headshot-file').val('')
      // this.createImage(files[0])
    },
    setImg (e, i) {
      let files = e.target.files || e.dataTransfer.files
      if (e.srcElement.files.item(0).size > (10 * 1024 * 1024)) {
        $('#file' + i).val('')
        alert('10MB')
      // if (e.srcElement.files.item(0).size > (4 * 1024 * 1024)) {
      //   alert('4MB')
      } else {
        // alert('Uploading: ' + e.srcElement.files.item(0).name + ' @ ' + e.srcElement.files.item(0).size + 'bytes')
        // console.log(window.URL.createObjectURL(e.srcElement.files.item(0)))
        // console.log(e.srcElement.files.item(0).name)
        // let target = e.target
        // console.log(this.uploadIMG[i].url)
        // this.uploadIMG[i].url = this.getFileUrl(e.srcElement)
        this.uploadIMG[i].url = window.URL.createObjectURL(e.srcElement.files.item(0))
        this.uploadIMG[i].file = files[0]
        this.uploadIMG[i].name = e.srcElement.files.item(0).name
        // $('#img_' + target.id).attr('src', this.getFileUrl(e.srcElement))
      }
    },
    cancelUploadIMG (i) {
      this.uploadIMG[i].file = this.uploadIMG[i].name = this.uploadIMG[i].url = ''
      $('#file' + i).val('')
      // this.createImage(files[0])
    },
    addUploadItem () {
      this.uploadIMG.push({ file: '', name: '', url: '' })
    },
    // getFileUrl (obj) {
    //   // console.log('getFileUrl')
    //   let url
    //   url = window.URL.createObjectURL(obj.files.item(0))
    //   // console.log(url)
    //   return url
    // },
    dosubmitVerifyBefore () {
      this.submitVerify = true
    },
    doSubmitVerify () {
      if (this.regEmailMessages.indexOf('') === -1 &&
      this.regEmail !== '' &&
      this.regPwdMessages.indexOf('') === -1 &&
      this.regPwd !== '' &&
      this.regPwdConfirmMessages.indexOf('') === -1 &&
      this.regPwdConfirm !== '' &&
      this.regMobileMessages.indexOf('') === -1 &&
      this.regMobile !== '' &&
      this.regNameMessages.indexOf('') === -1 &&
      this.regName !== '' &&
      this.regNicknameMessages.indexOf('') === -1 &&
      this.regGenderMessages.indexOf('') === -1 &&
      this.regGender !== '' &&
      this.regYearMessages.indexOf('') === -1 &&
      this.regYear !== '' &&
      this.regBirthMessages.indexOf('') === -1 &&
      this.regBirth !== '' &&
      this.regIntroMessages.indexOf('') === -1 &&
      this.regTagMessages.indexOf('') === -1 &&
      this.regEdu1Messages.indexOf('') === -1 &&
      this.regEdu1 !== '' &&
      this.regEdu2Messages.indexOf('') === -1 &&
      this.regEdu3Messages.indexOf('') === -1 &&
      this.regJobMessages.indexOf('') === -1 &&
      this.regExpMessages.indexOf('') === -1 &&
      this.regInvitationCodeMessages.indexOf('') === -1) {
        this.submit = true
      } else {
        this.submit = false
        scroll(0, 0)
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = ''
      }
    },
    dosubmitVerifyAfter () {
      if (this.submit) {
        this.loadingViewText = ''
        this.loadingView = true
        let formData = new FormData()
        formData.append('dummy_teacher_id', (this.timeID !== '') ? (this.timeID + this.regNickname + this.random) : '')
        formData.append('regEmail', this.regEmail)
        formData.append('regPwd', sha256(this.regPwd).toUpperCase())
        formData.append('regMobile', this.regMobile)
        formData.append('regName', this.regName)
        formData.append('regNickname', this.regNickname || this.regName)
        formData.append('regGender', this.regGender)
        formData.append('tutor_experience', this.regYear)
        formData.append('regBirth', this.regBirth)
        formData.append('intro', this.regIntro)
        formData.append('subject_type', this.regTag)
        formData.append('education_1', this.regEdu1)
        formData.append('education_2', this.regEdu2)
        formData.append('education_3', this.regEdu3)
        formData.append('company', this.regJob)
        formData.append('special_exp', this.regExp)
        formData.append('teacher_general_availabale_time', this.regSelectTime)
        formData.append('upload_snapshot', this.regHeadshot[0].file)
        formData.append('invitation_code', this.regInvitationCode)
        this.uploadIMG.forEach((up) => {
          formData.append('upload_cer', up.file)
        })
        this.$http.post('api/account/signupTeacher/', formData).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              this.doGtagsEvent('TeacherRegister-', '[]', this.regEmail)
              // scroll(0, 0)
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = '1~3'
              alert('!')
              setTimeout(() => {
                this.$router.push('/')
              }, 500)
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = ''
            }
          })
          .catch(err => {
            this.loadingView = false
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = ''
            console.log(err)
          })
      }
    },
    async doSubmit () {
      await this.dosubmitVerifyBefore()
      await this.doSubmitVerify()
      await this.dosubmitVerifyAfter()
    }
  }
}
</script>
