  },
  computed: {
    ...mapState(['root', 'appName', 'dologinView', 'deposit']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  watch: {
    userBank (val) {
      if (this.userBank !== '') {
        this.userBankMessages = ''
      } else {
        this.userBankMessages = ' '
      }
    },
    userBankAccount (val) {
      if (this.userBankAccount !== '') {
        this.userBankAccountMessages = ''
      } else {
        this.userBankAccountMessages = ' '
      }
    },
    userBankCode (val) {
      if (this.userBankCode !== '') {
        this.userBankCodeMessages = ''
        this.userBankNameList.forEach((b) => {
          if (b.value === val) {
            this.userBank = b.text
          }
        })
      } else {
        this.userBankCodeMessages = ' '
      }
    },
    dologinView (val) {
      if (!val) {
        this.$router.push('/')
      }
    }
  },
  mounted () {
    if (localStorage.getItem('token')) {
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              if (this.userInfo.type === 'teacher') {
                this.getExamBankOrderHistory()
                this.getLessonSalesHistory()
                this.getQPointsWtihdrawalHistory()
                this.getBankingInfomation()
              }
              if (this.userInfo.type === 'student') {
                this.getExamBankOrderHistory()
                this.getStudentOrderHistory()
                this.getQPointsWtihdrawalHistory()
                this.getBankingInfomation()
              }
            } else {
              this.$router.push('/')
            }
          } else if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
        .catch(err => {
          console.log(err)
          if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else {
      // this.$router.push('/')
      this.$store.state.dologinView = true
    }
  },
  methods: {
    test () {
      alert('')
    },
    getExamBankOrderHistory () {
      let body = {
        userID: (this.userInfo ? this.userInfo.user_id : -1),
        type: this.userInfo ? this.userInfo.type : ''
      }
      this.$http.post('/api/account_finance/examBankOrderHistory/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            this.questionBankInfo = res.data || {}
          } else if (res.status === 'failed') {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          } else {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = ''
          }
        })
        .catch(err => {
          this.snackbar = true
          this.color = 'orange'
          this.errorMessages = ''
          console.log(err)
        })
    },
    getLessonSalesHistory () {
      this.loadingSalesOrderStatus = true
      let body = {
        userID: (this.userInfo ? this.userInfo.user_id : -1),
        type: this.userInfo ? this.userInfo.type : ''
      }
      this.$http.post('/api/account_finance/getLessonSalesHistory/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          this.loadingSalesOrderStatus = false
          if (res.status === 'success') {
            this.salesOrderList = res.data
          } else if (res.status === 'failed') {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          } else {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = ''
          }
        })
        .catch(err => {
          this.snackbar = true
          this.color = 'orange'
          this.errorMessages = ''
          console.log(err)
        })
    },
    getStudentOrderHistory () {
      this.loadingBuyOrderStatus = true
      let body = {
        userID: (this.userInfo ? this.userInfo.user_id : -1),
        type: this.userInfo ? this.userInfo.type : ''
      }
      this.$http.post('/api/account_finance/studentOrderHistory/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          this.loadingBuyOrderStatus = false
          if (res.status === 'success') {
            this.buyOrderList = res.data
          } else if (res.status === 'failed') {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          } else {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = ''
          }
        })
        .catch(err => {
          this.snackbar = true
          this.color = 'orange'
          this.errorMessages = ''
          console.log(err)
        })
    },
    getQPointsWtihdrawalHistory () {
      this.loadingQPWithdrawalStatus = true
      let body = {
        userID: (this.userInfo ? this.userInfo.user_id : -1),
        type: this.userInfo ? this.userInfo.type : ''
      }
      this.$http.post('/api/account_finance/getQPointsWithdrawalHistory/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          this.loadingQPWithdrawalStatus = false
          if (res.status === 'success') {
            this.QPWithdrawalList = res.data
          } else if (res.status === 'failed') {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          } else {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = ''
          }
        })
        .catch(err => {
          this.snackbar = true
          this.color = 'orange'
          this.errorMessages = ''
          console.log(err)
        })
    },
    getBankingInfomation (type) {
      this.userBankCodeMessages = ''
      this.userBankMessages = ''
      this.userBankAccountMessages = ''
      let body = {
        userID: (this.userInfo ? this.userInfo.user_id : -1),
        type: this.userInfo ? this.userInfo.type : ''
      }
      this.$http.post('/api/account/getBankingInfomation/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            this.userBankCode = res.data.bank_code
            this.userBank = res.data.bank_name
            this.userBankAccount = res.data.bank_account_code
            this.userBalance = res.data.balance
            this.userWithholdingBalance = res.data.withholding_balance
            this.userTxnFee = res.data.txn_fee
            this.$store.state.deposit = res.data.balance
          } else if (res.status === 'failed') {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          } else {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = ''
          }
        })
        .catch(err => {
          this.snackbar = true
          this.color = 'orange'
          this.errorMessages = ''
          console.log(err)
        })
      if (type === 'only_editting') {
        this.userBankInfoView = true
      } else if (type === 'withdrawal_after_editting') {
        if (this.userBalance === 0) {
          alert('Q')
        } else if ((this.userBalance - this.userTxnFee) > 0) {
          this.withdrawalView = true
        } else {
          alert('Q')
        }
      }
    },
    getLessonReservationTime (lessonID, teacherID, salesCase) {
      if (localStorage.getItem('token') && this.userInfo.type === 'student') {
        let body = {
          userID: (this.userInfo ? this.userInfo.user_id : -1),
          lessonID: lessonID
        }
        this.$http.post('/api/lesson/getStudentsAvailableRemainingMinutes/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              if (res.data.all_available_remaining_minutes_of_this_lesson > 0) {
                if (res.data.student_has_unused_trial_lesson_sales_set && salesCase !== 'trial') {
                  alert('')
                }
                let reservation = {
                  teacherID: teacherID,
                  lessonID: lessonID,
                  time: res.data.student_has_unused_trial_lesson_sales_set ? 30 : res.data.all_available_remaining_minutes_of_this_lesson,
                  trial: res.data.student_has_unused_trial_lesson_sales_set
                }
                localStorage.setItem('reservation', JSON.stringify(reservation))
                this.$router.push('/lesson/appointment')
              } else {
                alert('')
              }
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = ''
            }
          })
          .catch(err => {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = ''
            console.log(err)
          })
      } else {
        alert('')
      }
    },
    openOrderBankInfo (val) {
      this.orderBankInfo = val.edony_bank_info
      this.orderBankInfoView = true
    },
    doOrderPayConfirm (val) {
      let isPayBankCode = /^[0-9]{5}$/
      if (isPayBankCode.test(val.pay_bank_code)) {
        this.loadingViewText = ''
        this.loadingView = true
        let body = {
          userID: (this.userInfo ? this.userInfo.user_id : -1),
          type: this.userInfo ? this.userInfo.type : '',
          purchase_recordID: val.purchase_recordID,
          status_update: 0,
          part_of_bank_account_code: val.pay_bank_code
        }
        this.$http.post('/api/account_finance/studentEditOrder/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = ''
              this.getStudentOrderHistory()
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = ''
            }
          })
          .catch(err => {
            this.loadingView = false
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = ''
            console.log(err)
          })
      } else {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = ''
      }
    },
    doQuestionBankOrderPayConfirm (val) {
      let isPayBankCode = /^[0-9]{5}$/
      if (isPayBankCode.test(val)) {
        this.loadingViewText = ''
        this.loadingView = true
        let body = {
          userID: (this.userInfo ? this.userInfo.user_id : -1),
          type: this.userInfo ? this.userInfo.type : '',
          // purchase_recordID: val.purchase_recordID,
          status_update: 0,
          part_of_bank_account_code: val
        }
        this.$http.post('/api/account_finance/examBankEditOrder/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = ''
              this.getExamBankOrderHistory()
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = ''
            }
          })
          .catch(err => {
            this.loadingView = false
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = ''
            console.log(err)
          })
      } else {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = ''
      }
    },
    doOrderCancel (id, action, text) {
      if (confirm('' + text + '')) {
        this.loadingViewText = text + ''
        this.loadingView = true
        let body = {
          userID: (this.userInfo ? this.userInfo.user_id : -1),
          type: this.userInfo ? this.userInfo.type : '',
          purchase_recordID: id,
          status_update: action,
          part_of_bank_account_code: ''
        }
        this.$http.post('/api/account_finance/studentEditOrder/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '' + text
              this.getStudentOrderHistory()
              this.getBankingInfomation()
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = ''
            }
          })
          .catch(err => {
            this.loadingView = false
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = ''
            console.log(err)
          })
      }
    },
    doQuestionBankOrderCancel (id, action, text) {
      if (confirm('' + text + '')) {
        this.loadingViewText = text + ''
        this.loadingView = true
        let body = {
          userID: (this.userInfo ? this.userInfo.user_id : -1),
          type: this.userInfo ? this.userInfo.type : '',
          // purchase_recordID: id,
          status_update: action,
          part_of_bank_account_code: ''
        }
        this.$http.post('/api/account_finance/examBankEditOrder/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '' + text
              this.getExamBankOrderHistory()
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = ''
            }
          })
          .catch(err => {
            this.loadingView = false
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = ''
            console.log(err)
          })
      }
    },
    doSubmitVerify () {
      let error = false
      if (this.userBank === '') {
        this.userBankMessages = ' '
        error = true
      }
      if (this.userBankAccount === '') {
        this.userBankAccountMessages = ' '
        error = true
      }
      if (this.userBankCode === '') {
        this.userBankCodeMessages = ' '
        error = true
      }
      return error
    },
    doEditUserBankInfo () {
      if (!this.doSubmitVerify()) {
        this.userBankInfoView = false
        this.loadingViewText = ''
        this.loadingView = true
        let body = {
          userID: (this.userInfo ? this.userInfo.user_id : -1),
          type: this.userInfo ? this.userInfo.type : '',
          bank_code: this.userBankCode,
          bank_name: this.userBank,
          bank_account_code: this.userBankAccount,
          action: 'only_editting',
          amount: 0
        }
        this.$http.post('/api/account_finance/withdrawQPoints/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = ''
            } else if (res.status === 'failed') {
              this.userBankInfoView = true
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.userBankInfoView = true
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = ''
            }
          })
          .catch(err => {
            this.loadingView = false
            this.userBankInfoView = true
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = ''
            console.log(err)
          })
      } else {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = ''
      }
    },
    doQPWithdrawal () {
      if (!this.doSubmitVerify()) {
        if (confirm('Q?')) {
          if ((this.userBalance - this.userTxnFee) > 0) {
            this.withdrawalView = false
            this.loadingViewText = ''
            this.loadingView = true
            let body = {
              userID: (this.userInfo ? this.userInfo.user_id : -1),
              type: this.userInfo ? this.userInfo.type : '',
              bank_code: this.userBankCode,
              bank_name: this.userBank,
              bank_account_code: this.userBankAccount,
              action: 'withdrawal_after_editting',
              amount: (this.userBalance - this.userTxnFee)
            }
            this.$http.post('/api/account_finance/withdrawQPoints/', qs.stringify(body)).then(res => res.data)
              .then(res => {
                this.loadingView = false
                if (res.status === 'success') {
                  this.snackbar = true
                  this.color = 'green'
                  this.errorMessages = ''
                  this.getQPointsWtihdrawalHistory()
                  this.getBankingInfomation()
                } else if (res.status === 'failed') {
                  this.withdrawalView = true
                  this.snackbar = true
                  this.color = 'red'
                  this.errorMessages = res.errMsg
                } else {
                  this.withdrawalView = true
                  this.snackbar = true
                  this.color = 'red'
                  this.errorMessages = ''
                }
              })
              .catch(err => {
                this.loadingView = false
                this.withdrawalView = true
                this.snackbar = true
                this.color = 'orange'
                this.errorMessages = ''
                console.log(err)
              })
          } else {
            alert('Q')
            this.withdrawalView = false
          }
        }
      } else {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = ''
      }
    }
  }
}
</script>
